import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { File } from "@ionic-native/file";
import pdfMake from "pdfmake/build/pdfmake";
import pdfFonts from "pdfmake/build/vfs_fonts";
import { Platform, ToastController, List } from 'ionic-angular';
import * as moment from 'moment';
/*
  Generated class for the ContratoProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ContratoProvider {

  constructor(public http: HttpClient,    
    private platform: Platform,
    private file: File,
    private toastCtrl: ToastController
    ) {
    console.log('Hello ContratoProvider Provider');
  }

  saveToDevice(data: any, savefile: any, prEntrega: any) {
    prEntrega = moment(prEntrega).format("DD/MM/YYYY");

    let self = this;
    self.file.writeFile(self.file.externalDataDirectory, savefile, data, {
      replace: false
    });
    const toast = self.toastCtrl.create({
      message: "Arquivo salvo no seu celular",
      duration: 3000,
      position: "bottom"
    });
    toast.present();
  }

  createPdf(dados: any, contrato: any) {
    
console.log('dados enviados para o provider: ', dados);
      let self = this;
      pdfMake.vfs = pdfFonts.pdfMake.vfs;
      var docDefinition = {
        content: [
          {
            image:
              "data:image/jpeg;base64,",
  
            style: "header",
            margin: [0, 20, 0, 0]
          },
  
          {
            text: "Fabiano Gomes da Lapa",
            style: "subheader",
            margin: [10, 0, 0, 3]
          },
          {
            text: "CNPJ: 12.405.428/001-01",
            style: "subheader",
            margin: [10, 0, 0, 3]
          },
          {
            text: "CPF: 260.590.808-98",
            style: "subheader",
            margin: [10, 0, 0, 3]
          },
          { text: "RG: 28.866.616-1", style: "subheader", margin: [10, 0, 0, 3] },
          { text: "TELS: (13) 98836-3024; (13) 98144-0084.", style: "subheader" },
  
          {
            text: "CONTRATO DE PRESTAÇÃO DE SERVIÇOS DE MARCENARIA",
            style: "header",
            margin: [0, 20, 0, 20]
          },
  
          //--------------------  Dados do cliente ----------------------------------------
          { text: "01 - CONTRATANTE:", style: "titles", margin: [0, 20, 0, 10] },
          {
            text: `${dados.nome}`.toUpperCase(),
            style: "story",
            margin: [10, 10, 0, 0]
          },
          {
            text: `CPF: ${contrato.cpf}`.toUpperCase(),
            style: "story",
            margin: [10, 10, 0, 0]
          },
          {
            text: `Cep: ${dados.cep}`.toUpperCase(),
            style: "story",
            margin: [10, 10, 0, 0]
          },
          {
            text: `Endereço: ${dados.endereco}`.toUpperCase(),
            style: "story",
            margin: [10, 10, 0, 0]
          },
          {
            text: `Bairro: ${dados.bairro}`.toUpperCase(),
            style: "story",
            margin: [10, 10, 0, 0]
          },
          {
            text: `Cidade: ${dados.cidade}`.toUpperCase(),
            style: "story",
            margin: [10, 10, 0, 0]
          },
          {
            text: `Telefone: ${dados.tel}`.toUpperCase(),
            style: "story",
            margin: [10, 10, 0, 0]
          },
  
          //--------------Dados da empresa -------------------------------------
          { text: "02 - CONTRATADO:", style: "titles", margin: [0, 20, 0, 0] },
          {
            text:
              "Marcenaria Nova Design, representada por Fabiano Gomes da Lapa.",
            style: "story",
            margin: [10, 10, 0, 0]
          },
          {
            text:
              "Endereço (centro de corte e apoio baixada): Avenida marginal um, 102",
            style: "story",
            margin: [10, 10, 0, 0]
          },
          {
            text: "Cidade: São vicente/SP",
            style: "story",
            margin: [10, 10, 0, 0]
          },
  
          //-----------------------Descrição do serviço ------------------------------------------
          { text: "03 - DESCRITIVO:", style: "titles", margin: [0, 20, 0, 0] },
          {
            text: dados.descritivo,
            style: "story",
            margin: [10, 10, 0, 0]
          },
  
          //------------------------------- Valor Total ------------------------------------------
          { text: "04 - VALOR TOTAL:", style: "titles", margin: [0, 20, 0, 0] },
          {
            text: `R$ ${dados.vlTotal}`,
            style: "story",
            margin: [10, 10, 0, 0]
          },
          //------------------------------Prazo de entrega --------------------------------------
          {
            text: `05 - PRAZO DE ENTREGA:`,
            style: "titles",
            margin: [0, 20, 0, 0]
          },
          {
            text: `${contrato.prEntrega}`,
            style: "story",
            margin: [10, 10, 0, 0]
          },
  
          //--------------------------------Tratados ------------------------------------
          { text: `06:`, style: "titles", margin: [0, 20, 0, 0] },
          {
            text:
              "O CONTRATADO FORNCERÁ AS FERRAMENTAS, METERIAIS E PROFISSIONAIS NECESSÁRIOS PARA A EXECUÇÃO DO SERVIÇO, REPONSABILIZANDO-SE INCLUSIVE DA LIMPEZA QUE FOR NECESSÁRIA.",
            style: "story",
            margin: [10, 10, 0, 0]
          },
  
          //--------------------------------Tratados ------------------------------------
          { text: "07 - ENCANAMENTO:", style: "titles", margin: [0, 20, 0, 0] },
          {
            text:
              "FICA DE RESPONSABILIDADE DO CONTRATANTE FORNECER A PLANTA HIDRÁULICA DO APARTAMENTO DOIS DIAS ANTES DA MONTAGEM DOS MÓVEIS, FICANDO O CONTRATADO ISENTO DE QUAISQUER ÔNUS DECORRENTE DE CANO FURADO.",
            style: "story",
            margin: [10, 10, 0, 0]
          },
  
          //--------------------------------Tratados ------------------------------------
          { text: "08:", style: "titles", margin: [0, 20, 0, 0] },
          {
            text:
              "O CONTRATADO REPONDERÁ POR DANOS QUE RESULTEM DA IMPERÍCIA OU NEGLIGÊNCIA SUA OU DE SEUS EMPREGADOS, SEGUNDO OS PRINCÍPIOS GERAIS DE RESPONSABILIDADE.",
            style: "story",
            margin: [10, 10, 0, 0]
          },
  
          //--------------------------------Tratados ------------------------------------
          { text: "09:", style: "titles", margin: [0, 20, 0, 0] },
          {
            text:
              "AS PARTES ELEGEM O FÓRUM DE SÃO VICENTE/SP, PARA DIRIMIREM QUAISQUER DÚVIDAS DECORRENTES DO PRESENTE COTNRATO.",
            style: "story",
            margin: [10, 10, 0, 0]
          },
  
          //--------------------------------Tratados ------------------------------------
          { text: "10:", style: "titles", margin: [0, 20, 0, 0] },
          {
            text:
              "CONTRATANTE E CONTRATADO CONCORDAM E ESTÃO AJUSTADOS COM TODAS AS NORMAS E CLAÚSULAS ESTIPULADAS NESTE CONTRATO, E ASSIM ASSINAM O PRESENTE TERMO EM DUAS VIAS DE IGUAL TEOR, OS QUAIS PASSAM A TER FORÇA LEGAL ENTRE AS PARTES.",
            style: "story",
            margin: [10, 10, 0, 0]
          },
  
          //--------------------------------Tratados ------------------------------------
          {
            text: "__________________________________________________",
            style: "assinatura",
            margin: [0, 100, 0, 0]
          },
          {
            text: "Assinatura do contratante",
            style: "assinatura",
            margin: [0, 20, 0, 0]
          },
          {
            text: "__________________________________________________",
            style: "assinatura",
            margin: [0, 100, 0, 0]
          },
          {
            text: "Assinatura do contratado",
            style: "assinatura",
            margin: [0, 20, 0, 0]
          }
        ],
  
        styles: {
          header: {
            bold: true,
            fontSize: 14,
            alignment: "center"
          },
          assinatura: {
            fontSize: 14,
            alignment: "center"
          },
          subheader: {
            fontSize: 14,
            alignment: "center"
          },
  
          titles: {
            fontSize: 14,
            bold: true,
            alignment: "left"
          },
          story: {
            fontSize: 14,
            alignment: "left"
          },
          url: {
            fontSize: 14,
            alignment: "center"
          }
        },
        pageSize: "A4",
        pageOrientation: "portrait"
      };
  
      pdfMake.createPdf(docDefinition).getBuffer(buffer => {
        if (this.platform.is("cordova")) {
          let utf8 = new Uint8Array(buffer);
          let binaryArray = utf8.buffer;
          self.saveToDevice(binaryArray, `${contrato.nmContrato}.pdf`, dados.prEntrega);
        } else {
          pdfMake.createPdf(docDefinition).download();
        }
      });
    }

}



